              diagram_text_type = 'pie'
              break;
          default:
              diagram_text_type = 'rings'
              break;
      }
      
      for (let i = 0; i < arr_size ; i++) 
      {
          if(parseInt(arr[i]) > parseInt(first)) 
          {
              first = arr[i]; 
              first_index = i;
          } 
      }
      for(let i2 = 0; i2 < arr_size; i2++) {
