            <h4>Min energi</h4>\
            Rent praktiskt lägger jag min energi på att strukturera upp en trygg och förutsägbar omgivning. Mentalt brukar jag lägga fokus på att utvärdera trygghet och osäkerhet, mycket för att säkerställa trygghet.\
            <h4>Min hälsa</h4>\
            Jag tycker om att ta hand om mig själv och min hälsa, men mitt behov av trygghet kan leda till att jag småäter eller s.k tröstäter för mycket.\
            <h4>Vad jag undviker</h4>\
            Jag tenderar att undvika nytt folk och nya erfarenheter. Jag undviker relationer som inte från första början garanterar att båda parter hänger sig totalt. Jag undviker gärna sådant som verkar hotfullt och har generellt rädsla för att tappa kontrollen.\
            <h4>Mina styrkor</h4>\
            Jag är strukturerad, pålitlig och har egenskapen att kunna skapa en arbetsplats som är omtyckt och där andra känner sig trygga och säkra i miljön.\
            <h4>Mitt sätt att kommunicera</h4>\
            Jag talar om hur jag mår på insidan och betonar oftast om jag känner mig rotad, säker och trygg. Jag använder ofta ord som: tryggt, säkert, stabil och förutsägbar. Jag är ofta rak och kort i mitt sätt att kommunicera på och vill gärna att man tar upp det viktigaste först. Det är lätt att urskilja mig bland folkmassorna eftersom jag gestikulerar med mina armar när jag pratar.\
            <h4>Min stress</h4>\
            Jag känner mig ofta stressad när det krävs något nytt av mig och det blir oftast värre när jag inte är förberedd på det. Jag har svårt för snabba och oväntade förändringar, även de som leder till något bättre, de gör mig nervös och stressad. Jag sätter press på mig själv för att säkerställa att jag inte känner mig osäker, detta tar dessutom energi från mig och jag kan känna mig trött av det.\
            <h4>Mina försvarsmekanismer</h4>\
            Jag kan lätt gå till försvar när någon kräver att jag ska ändra på mina vanor, rutiner eller om jag tvingas att handskas med nya människor och situationer. Jag kan bli irriterad och frustrerad om andra utmanar mitt behov av att ha trygghet eller förutsägbart.\
            <h4>Mina känslor</h4>\
            Framtiden är något jag ofta kan oroa över och ställer frågor kring den för att känna mig säker. Jag kan också vara känslig för faror och upplever rädsla kring sådant som kan hända.\
            <br><br>\
            <h2>Utveckling och Balans</h2>\
                <mark><h4>Mitt mål</h4>\
                Mitt mål är att kunna vara mer i själva nuet - att vara mer närvarande. Jag behöver lära mig att släppa tanken på vad som kan hända och börja ta ”risker” (förnuftiga), utmana mig själv mer för att kunna göra de framsteg jag innerligt önskar göra. Det är även viktigt att jag inser att ovisshet, osäkerhet och oförutsägbarhet är en del av livet - det går inte kontrollera allt.\
                <h4>Mina åtgärder</h4>\
                Jag måste börja tänja på mina gränser genom att uppleva nya saker och gå mer utanför trygghetszonen. Istället för att lägga tankar på oro ska jag fokusera mer på uppleva glädje, nuet och sådant jag uppskattar i livet.\
                Jag behöver bli bättre på att lära mig se fram emot och njuta av utmaningarna och vad de faktiskt kan bidra med och hur jag utvecklas av dem. Detta börjar med att jag måste utveckla ett inre mod för att kunna agera även när jag känner mig osäker, det är denna osäkerhet jag måste omfamna för att anskaffa mig modet. Jag behöver också bli bättre på att ta hjälp av att andras åsikter utan att gå i försvar, utan ta in det, reflektera och implementera det i handling.\
                <h4>Vad kommer mellan mig och mina mål</h4>\
                Min djupa önskan är att känna mig så säker som möjligt och planera inför framtiden vilket står i vägen för mina mål. Jag kan vara överbeskyddande och kontrollerande eftersom jag söker efter mycket trygghet och säkerhet. Detta kan också leda till att jag ser framtiden med pessimistiska ögon och står därför mellan mig och mina mål.\
                <h4>Vad kan andra göra för att stötta mig att nå mina mål</h4>\
                Andra kan stötta mig genom att introducera mig för nya saker, uppgifter och upplevelser och med detta utmana mig mer. Viktigt att de vågar utmana mig. De kan även uppmuntra min spontanitet för att jag ska ha mer kul och uppleva mer glädje - livets nektar. Andra kan även hjälpa mig att komma över mina tvivel med hjälp av realistiska argument och ibland komma med fakta som övertygar mig.\
                </mark>            "
      }
      else if(first_index == 1) { 
          this.primaryText = "<h2>Mitt primära behov är <b>variation</b></h2>\
            <h4>Min övertygelse</h4>\
            Jag har en övertygelse om att glädje och lycka hänger samman med att få uppleva olika utmaningar och äventyr med mycket variation. Detta utvecklar och utmanar mitt emotionella, intellektuella och fysiska jag. Mitt känslomässiga välbehag är i stort sett beroende av att uppleva spänning och till en viss grad ovisshet.\
