</template>

<script>
  export default {
  // ========================
  data: () => {
    return {
    timer: null,
    totalTime: (14 * 60),
    resetButton: false,
    title: "",
    minutesLeft: 14,
    timerOn: false,
    }
  },
  // ========================
  methods: {
    startTimer: function() {
      this.timer = setInterval(() => this.countdown(), 1000);
      this.resetButton = true;
      this.title = ""
      document.getElementById('timer').style.visibility = 'visible';
    },
    
    stopTimer: function() {
      clearInterval(this.timer);
      this.timer = null;
      this.resetButton = true;
      this.title = ""
      document.getElementById('clock-text').style.opacity = 0.1;
    },
    
    resetTimer: function() {
      this.totalTime = (25 * 60);
      clearInterval(this.timer);
      this.timer = null;
      this.resetButton = false;
      this.title = ""
    },
    
    padTime: function(time) {
      return (time < 10 ? '0' : '') + time;
    },
    countdown: function() {
      if(this.totalTime >= 1){
        this.totalTime--;
      } else{
        this.totalTime = 0;
        this.resetTimer()
      }
    }
  },
  watch: {
    minutesLeft: function() {
      if(this.minutesLeft < 7 && this.minutesLeft > 3) {
        document.getElementById('clock-text').style.background = 'yellow';
        document.getElementById('clock-text').color = 'black';
      }
      else if(this.minutesLeft < 3) {
        document.getElementById('clock-text').style.background = 'red';
        document.getElementById('clock-text').color = 'white';
      } 
 
    }
  },
  // ========================
  computed: {
    minutes: function() {
      const minutes = Math.floor(this.totalTime / 60);
      this.minutesLeft = minutes;
      return this.padTime(minutes);
    },
    seconds: function() {
      const seconds = this.totalTime - (this.minutes * 60);
      return this.padTime(seconds);
    }
  }
}
  

</script>

